<template>
  <div class="bookstore-detail">
    <div class="book-introduction">
      <div class="left-demo-card">
        <div class="left-demo-body">
          <div class="left-img-box flex-center">
            <img
              :src="coverFiles.length > 0 ? coverFiles[currImg].fullUrl : ''"
              alt=""
            />
          </div>
          <div class="left-small-img">
            <img
              v-for="(item, index) in coverFiles"
              :key="index + Math.random()"
              :src="item.fullUrl"
              :class="index == currImg ? 'demo-card-box-img-checked' : ''"
              @click="changeSrc(index)"
              alt=""
            />
          </div>
        </div>
        <div class="left-demo-bottom">
          <div @click="addBookshelf()">
            <img
              :src="
                require('@/assets/icon/book1' +
                  (isBookshelf ? 'ed.png' : '.png'))
              "
              alt=""
            />
            {{ isBookshelf ? "已加入书架" : "加入书架" }}
          </div>
          <!-- <div @click="phoneLook()">
            <img src="@/assets/icon/idcode.png" alt="" />
            手机端观看
          </div> -->
        </div>
      </div>
      <div class="right-info">
        <div class="title">
          <div class="book-label">
            {{ callDictionaries(goodsTypeList, goodsInfo.resourceType) }}
          </div>
          <span>{{ goodsInfo.goodsTitle }}</span>
        </div>
        <div class="book-intro" v-if="goodsInfo.authorRecommendation">
          <div
            class="defaultIntro"
            @mouseenter="enterShow"
            @mouseleave="leaveShow"
          >
            <span class="twointro"
              >{{ goodsInfo.authorRecommendation | ellipsis(120) }}
              <span
                class="font"
                v-if="goodsInfo.authorRecommendation.length > 120"
                >更多</span
              ></span
            >
            <div v-if="showMore" class="allIntro">
              <span>{{ goodsInfo.authorRecommendation }}</span>
            </div>
          </div>
        </div>
        <div class="label">
          <span v-for="(item, index) in keyword" :key="index">{{ item }}</span>
        </div>
        <div class="book-intro-info">
          <div
            v-if="
              goodsInfo.goodsType != 'paper' &&
              goodsInfo.goodsType != 'reference' &&
              goodsInfo.goodsType != 'other' &&
              goodsInfo.goodsType != 'looseleafOrtextbook'
            "
          >
            <span>作者：</span>
            <el-tooltip
              :content="goodsInfo.author"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                goodsInfo.author == null || goodsInfo.author == ""
                  ? "-"
                  : goodsInfo.author
              }}</span>
            </el-tooltip>
          </div>
          <div
            v-if="
              goodsInfo.goodsType != 'paper' &&
              goodsInfo.goodsType != 'reference' &&
              goodsInfo.goodsType != 'other' &&
              goodsInfo.goodsType != 'looseleafOrtextbook'
            "
          >
            <span>出版社：</span>
            <el-tooltip
              :content="goodsInfo.publishingHouse"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                goodsInfo.publishingHouse == null ||
                goodsInfo.publishingHouse == ""
                  ? "-"
                  : goodsInfo.publishingHouse
              }}</span>
            </el-tooltip>
          </div>
          <div
            v-if="
              goodsInfo.goodsType != 'paper' &&
              goodsInfo.goodsType != 'reference' &&
              goodsInfo.goodsType != 'learning' &&
              goodsInfo.goodsType != 'other' &&
              goodsInfo.goodsType != 'looseleafOrtextbook'
            "
          >
            <span>出版时间：</span>
            <span class="ellipsis-aRow">{{
              goodsInfo.publishDate == null || goodsInfo.publishDate == ""
                ? "-"
                : goodsInfo.publishDate
            }}</span>
          </div>
          <div v-if="goodsInfo.goodsType == 'learning'">
            <span>ISSN：</span>
            <el-tooltip
              :content="goodsInfo.isbn"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                goodsInfo.isbn == null || goodsInfo.isbn == ""
                  ? "-"
                  : goodsInfo.isbn
              }}</span>
            </el-tooltip>
          </div>
          <div v-if="goodsInfo.goodsType == 'paper'">
            <span>教学属性：</span>
            <span class="ellipsis-aRow">{{
              goodsInfo.teachPropertyAllStr == null ||
              goodsInfo.teachPropertyAllStr == ""
                ? "-"
                : goodsInfo.teachPropertyAllStr
            }}</span>
          </div>
          <div v-if="goodsInfo.goodsType == 'paper'">
            <span>学科：</span>
            <span class="ellipsis-aRow">{{
              goodsInfo.subjectName == null || goodsInfo.subjectName == ""
                ? "-"
                : goodsInfo.subjectName
            }}</span>
          </div>
          <div v-if="goodsInfo.goodsType == 'paper'">
            <span>考试时长：</span>
            <span class="ellipsis-aRow"
              >{{
                goodsInfo.timeCount == null || goodsInfo.timeCount == ""
                  ? "-"
                  : goodsInfo.timeCount
              }}分钟</span
            >
          </div>
        </div>
        <div class="book-intro-info">
          <div
            v-if="
              goodsInfo.goodsType == 'reference' ||
              goodsInfo.goodsType == 'other'
            "
          >
            <span>作者：</span>
            <el-tooltip
              :content="goodsInfo.author"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                goodsInfo.author == null || goodsInfo.author == ""
                  ? "-"
                  : goodsInfo.author
              }}</span>
            </el-tooltip>
          </div>
          <div
            v-if="
              goodsInfo.goodsType != 'paper' &&
              goodsInfo.goodsType != 'reference' &&
              goodsInfo.goodsType != 'learning' &&
              goodsInfo.goodsType != 'other'
            "
          >
            <span>ISBN：</span>
            <el-tooltip
              :content="goodsInfo.isbn"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                goodsInfo.isbn == null || goodsInfo.isbn == ""
                  ? "-"
                  : goodsInfo.isbn
              }}</span>
            </el-tooltip>
          </div>
          <div v-if="goodsInfo.goodsType == 'learning'">
            <span>CN：</span>
            <el-tooltip
              :content="goodsInfo.cn"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                goodsInfo.cn == null || goodsInfo.cn == "" ? "-" : goodsInfo.cn
              }}</span>
            </el-tooltip>
          </div>
          <div v-if="goodsInfo.goodsType != 'paper'">
            <span>教学属性：</span>
            <span class="ellipsis-aRow">{{
              goodsInfo.teachPropertyAllStr == null ||
              goodsInfo.teachPropertyAllStr == ""
                ? "-"
                : goodsInfo.teachPropertyAllStr
            }}</span>
          </div>
          <div v-if="goodsInfo.goodsType != 'paper'">
            <span>提供机构：</span>
            <el-tooltip
              :content="goodsInfo.instName"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                goodsInfo.instName == null || goodsInfo.instName == ""
                  ? "-"
                  : goodsInfo.instName
              }}</span>
            </el-tooltip>
          </div>
          <div v-if="goodsInfo.goodsType == 'paper'">
            <span>题目数量：</span>
            <span class="ellipsis-aRow">{{
              goodsInfo.questionCount == null || goodsInfo.questionCount == ""
                ? "-"
                : goodsInfo.questionCount
            }}</span>
          </div>
          <div v-if="goodsInfo.goodsType == 'paper'">
            <span>满分：</span>
            <span class="ellipsis-aRow"
              >{{
                goodsInfo.fullScore == null || goodsInfo.fullScore == ""
                  ? "-"
                  : goodsInfo.fullScore
              }}分</span
            >
          </div>
          <div v-if="goodsInfo.goodsType == 'paper'">
            <span>及格分：</span>
            <span class="ellipsis-aRow"
              >{{
                goodsInfo.passingScore == null || goodsInfo.passingScore == ""
                  ? "-"
                  : goodsInfo.passingScore
              }}分</span
            >
          </div>
        </div>
        <div class="price">
          <span v-if="!isShop">{{ goodsInfo.realPrice | tofixed2("￥") }}</span>
          <span v-if="isShop" class="havebuy">已购买</span>
          <span
            v-if="goodsInfo.prices > 0"
            :class="goodsInfo.prices == 0 ? 'yprice2' : 'yprice'"
            >{{ goodsInfo.prices | tofixed2("￥") }}</span
          >
        </div>
        <div class="sales">
          <div class="sales-volume">
            <!-- <img src="@/assets/icon/quan.png" alt="" />
            优惠：
            <div class="quan1">满50-5</div>
            <div class="quan2">满59打9.5折</div>
            <span @click="getMore">领取更多&gt;</span> -->
          </div>
          <div class="goods-score">
            商品评分：
            <el-rate
              v-model="goodsInfo.averageScore"
              disabled
              show-score
              text-color="#D81E06"
              :colors="colors"
              score-template="{value}分"
            >
            </el-rate>
          </div>
        </div>
        <div class="integral-share">
          <div class="double-sales">
            <div class="sales-volume">
              <img src="@/assets/icon/销量预测.png" alt="" />
              销量：{{
                goodsInfo.salesVolume == null || goodsInfo.salesVolume == 0
                  ? 0
                  : goodsInfo.salesVolume
              }}
            </div>
            <div class="integral">
              <!-- <img src="@/assets/icon/积分.png" alt="" />
              赠送积分：15 -->
            </div>
          </div>
          <div class="share-img">
            分享到
            <share :config="$store.state.read.config" @click="shareBook" />
            <!-- <img @click="share" src="@/assets/icon/qq.png" alt="" />
            <img @click="share" src="@/assets/icon/wechat.png" alt="" />
            <img @click="share" src="@/assets/icon/weibo.png" alt="" /> -->
          </div>
        </div>
        <el-divider></el-divider>
        <div class="buttons">
          <!-- 如果商品是内部上架状态，则禁用这些按钮 -->
          <el-button
            v-if="!isShop && goodsInfo.status != 5 && goodsInfo.status != 6"
            :disabled="goodsInfo.status == 3"
            :class="goodsInfo.status == 3 ? 'border-disable' : ''"
            @click="onlinePreview(true)"
            >在线预览</el-button
          >
          <el-button
            v-if="!isShop && goodsInfo.status != 5 && goodsInfo.status != 6"
            :disabled="goodsInfo.status != 2"
            :class="goodsInfo.status != 2 ? 'bgcolor-disalbe' : ''"
            type="primary"
            @click="toPay()"
            >立即购买</el-button
          >
          <el-button
            v-if="!isShop && goodsInfo.status != 5 && goodsInfo.status != 6"
            :disabled="goodsInfo.status != 2"
            :class="goodsInfo.status != 2 ? 'border-disable' : ''"
            @click="addCart()"
            ><img src="@/assets/icon/buycart.png" alt="" />加入购物车</el-button
          >
          <button
            class="sucessbtn"
            type="success"
            v-if="
              goodsType != 'paper' &&
              isShop &&
              goodsInfo.status != 5 &&
              goodsInfo.status != 6
            "
            @click="onlinePreview(false)"
          >
            立即浏览
          </button>
          <button
            class="sucessbtn"
            type="success"
            v-if="
              goodsType == 'paper' &&
              isAnswer &&
              goodsInfo.status != 5 &&
              goodsInfo.status != 6
            "
            @click="goAnswer()"
          >
            开始答题
          </button>
          <button
            class="offShelfbtn"
            v-if="goodsInfo.status == 5 || goodsInfo.status == 6"
          >
            已下架
          </button>
        </div>
        <p>温馨提示：此类商品不支持退换货</p>
      </div>
    </div>
    <div class="book-infos">
      <div class="left-book-detail">
        <el-tabs v-model="activeName">
          <!-- 内容简介 -->
          <el-tab-pane name="first">
            <span slot="label">内容简介</span>
            <content-intro
              :import-type="goodsType"
              :goods-info="goodsInfo"
              :source-list="sourceList"
              :subject-id="subjectId"
              :goods-id="goodsId"
              :pthis="pthis"
            ></content-intro>
          </el-tab-pane>
          <!-- 图书目录 -->
          <el-tab-pane
            name="second"
            v-if="
              goodsInfo.goodsType != 'paper' &&
              goodsInfo.goodsType != 'looseleafOrtextbook'
            "
          >
            <span slot="label">图书目录</span>
            <div class="category" v-if="goodsInfo.directoryStructure != null">
              <el-input
                type="textarea"
                class="textareas mulu"
                :readonly="true"
                :value="goodsInfo.directoryStructure"
                :autosize="{ minRows: 0 }"
              ></el-input>
            </div>

            <noData
              v-if="
                goodsInfo.directoryStructure == '' ||
                goodsInfo.directoryStructure == null
              "
            ></noData>
          </el-tab-pane>
          <!-- 作者介绍 -->
          <el-tab-pane
            name="third"
            v-if="
              goodsInfo.goodsType != 'paper' &&
              goodsInfo.goodsType != 'looseleafOrtextbook'
            "
          >
            <span slot="label">作者介绍</span>
            <author-intro :data-list="authorList"></author-intro>
            <noData
              v-if="authorList == null || authorList.length == 0"
            ></noData>
          </el-tab-pane>
          <!-- 评价 -->
          <el-tab-pane name="fourth">
            <span slot="label">评价 ( {{ total }} )</span>
            <evaluate-content
              :data-list="evaluateList"
              @refreshList2="refreshList2"
            ></evaluate-content>
            <noData v-if="total == 0"></noData>
            <div class="pagination-box" v-show="total > 0">
              <el-pagination
                class="ou-pagination"
                background
                hide-on-single-page
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryParams.pageNum"
                :page-sizes="[10, 20, 30, 40, 50]"
                :page-size="queryParams.pageSize"
                layout="total, prev, pager, next, sizes, jumper, slot"
                :total="total"
              >
                <el-button @click="getList2()" class="ou-page-go">GO</el-button>
              </el-pagination>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
      <div class="right-recommend">
        <div class="recommend-special" v-if="recommendSubject.length > 0">
          <div class="title theme-title">推荐专题</div>
          <div
            class="recommend-book"
            v-for="(item, index) in recommendSubject"
            :key="index"
          >
            <div class="img flex-center" @click="toDetail(item.subjectId)">
              <img :src="item.cover" alt="" />
            </div>
            <el-tooltip
              :content="item.subjectName"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <div class="book-title" @click="toDetail(item.subjectId)">
                {{ item.subjectName }}
              </div>
            </el-tooltip>
            <div class="book-info">
              <div class="left-label">共{{ item.resourcesNun }}商品</div>
              <div class="price">
                <span class="sellprice ellipsis-aRow">{{
                  item.sellingPrice | tofixed2("￥")
                }}</span>
                <span
                  class="ellipsis-aRow"
                  :class="item.price == 0 ? 'yprice2' : 'yprice'"
                  >{{ item.price | tofixed2("￥") }}</span
                >
              </div>
            </div>
          </div>
        </div>
        <div class="recommend-resource" v-if="recommendGoodsOne.length > 0">
          <div class="title theme-title">推荐商品</div>
          <div
            class="recommend-book"
            v-for="(item, index) in recommendGoodsOne"
            :key="index"
          >
            <div
              class="img flex-center"
              @click="toDetail1(item.goodsId, item.goodsType)"
            >
              <img :src="item.cover" alt="" />
            </div>
            <div
              class="book-title"
              @click="toDetail1(item.goodsId, item.goodsType)"
            >
              <img src="@/assets/icon/title.png" alt="" />
              <el-tooltip
                :content="item.goodsTitle"
                placement="top"
                v-ak-tooltip-auto-show
              >
                <p>{{ item.goodsTitle }}</p>
              </el-tooltip>
            </div>
            <div class="catalogue">
              <div
                class="content-item"
                v-for="(item, index) in recommendGoods"
                :key="index"
                @click="toDetail1(item.goodsId, item.goodsType)"
              >
                <el-tooltip
                  :content="item.goodsTitle"
                  placement="top"
                  v-ak-tooltip-auto-show
                >
                  <span class="ellipsis-aRow">{{ item.goodsTitle }} </span>
                </el-tooltip>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { OuchnOss } from "@/plugins/OuchnOss";
import {
  goodsInfoDetail,
  recommendGoods,
  recommendSubject,
  Listevaluate,
  CartAdd,
  BookshelfAdd,
  IsCartAdd,
  IsBookshelfAdd,
  checkIsBuy,
  delBookshelf,
  SelGoodsStatus,
  teachingAttrList,
} from "@/api/bookstore/bookstore";
import evaluateContent from "@/views/bookstore/detail/evaluateContent";
import authorIntro from "@/views/bookstore/detail/authorIntro";
import contentIntro from "@/views/bookstore/detail/contentIntro";
import noData from "@/components/noData.vue";
export default {
  data() {
    return {
      isShop: false,
      isAnswer: false,
      subjectId: null,
      showMore: false,
      colors: ["#D81E06", "#D81E06", "#D81E06"],
      isfold: true,
      isflag: 1,
      isBookshelf: false,
      isCart: false,
      keyword: [],
      goodsId: null,
      goodsType: null,
      goodsInfo: {}, //商品基本信息
      sourceList: [], //资源文件
      authorInfo: {}, //作者简介
      total: 0,
      currentPage: 1,
      activeName: "first",
      value: 4.7,
      currImg: 0,
      evaluateList: [],
      coverFiles: [], //左侧商品图片数组
      recommendGoods: [], //推荐商品
      recommendGoodsOne: [],
      recommendSubject: [], //推荐专题
      authorList: [], //作者简介
      //查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        goodsId: null,
        // type: "subject",
      },
      query: {
        goodsId: null,
        type: "goods",
      },
      payType: "goods",
      ouchnoss1: null,
      goodsTypeList: [],
      addCartState: true,
      courseList: [],
      paperList: [],
    };
  },
  computed: {
    pthis() {
      return this;
    },
  },
  components: {
    evaluateContent,
    authorIntro,
    contentIntro,
    noData,
  },
  created() {
    this.$store.commit("subjectId_book", this.$route.query.goodsId);
    this.goodsId = this.$route.query.goodsId;
    this.goodsType = this.$route.query.goodsType;
    this.subjectId = this.$route.query.subjectId;
    this.getList();
    this.getList2();
    this.getoc_resource_file();
    if (localStorage.getItem("USERID")) {
      //判断是否加入书架
      this.isAddBookshelf();
      //判断是否加入购物车
      this.isAddCart();
    }
    this.ouchnoss1 = new OuchnOss(true);
  },
  watch: {
    //监听路由变化
    $route() {
      if (this.$route.query.goodsId) {
        this.$store.commit("subjectId_book", this.$route.query.goodsId);
        this.goodsId = this.$route.query.goodsId;
        this.goodsType = this.$route.query.goodsType;
        this.isCart = false; //切换商品后未加入购物车时会提示已加入购物车
        this.getList();
        this.getList2();
      }
    },
  },
  methods: {
    //领取更多
    getMore() {
      this.$alert("此功能正在开发中，敬请期待", "提示信息");
    },
    enterShow() {
      if (this.goodsInfo.authorRecommendation.length > 135) {
        this.showMore = true;
      }
    },
    leaveShow() {
      this.showMore = false;
    },
    getList() {
      //商品基本信息
      goodsInfoDetail(this.goodsId).then((res) => {
        if (res.code == 200) {
          this.goodsInfo = res.data.goods;
          this.sourceList = res.data.sourceList;
          !this.goodsInfo.averageScore
            ? (this.goodsInfo.averageScore = 5)
            : this.goodsInfo.averageScore;
          if (this.goodsInfo.keyword) {
            this.keyword = this.goodsInfo.keyword.split(",");
          }
          this.coverFiles = res.data.coverFiles;
          if (this.coverFiles.length == 0) {
            let data = {
              fullUrl: require("@/assets/images/default_img.jpg"),
            };
            this.coverFiles.push(data);
          }
          this.authorList = res.data.authorList;
          let userId = this.$store.getters.userId;
          if (userId) {
            this.isPayGoods();
          }
        }
      });
      //推荐专题
      recommendSubject(this.goodsId).then((res) => {
        if (res.code == 200) {
          this.recommendSubject = res.data.slice(0, 2);
        }
      });
      //推荐商品
      recommendGoods(this.goodsId).then((res) => {
        if (res.code == 200) {
          this.recommendGoodsOne = res.data.slice(0, 1);
          this.recommendGoods = res.data.slice(1, 5);
        }
      });
    },
    //点击查看更多
    open() {
      this.isfold = !this.isfold;
      this.isfold === true ? (this.isflag = 1) : (this.isflag = 2);
    },
    shareBook() {},
    //获取评价列表
    getList2() {
      this.queryParams.goodsId = this.goodsId;
      Listevaluate(this.queryParams).then((res) => {
        if (res.code == 200) {
          this.evaluateList = res.rows;
          this.evaluateList.forEach((item) => {
            item.score = Number(item.score);
          });
          this.total = res.total;
        }
      });
    },
    //跳转到对应专题
    toDetail(id) {
      // console.log(id)
      this.$router.push({
        path: "/special/specialDetail",
        query: { subjectId: id },
      });
    },
    //跳转到对应商品
    toDetail1(id, type) {
      // console.log(id,type)
      this.$router.push({
        path: "/bookstore/bookstoreDetail",
        query: { goodsId: id, goodsType: type },
      });
    },
    //跳转到订单页
    toPay() {
      let userId = this.$store.getters.userId;
      if (!userId) {
        // let paramsJson = JSON.stringify({ goodsId: this.goodsId, goodsType: this.goodsType })
        this.$store.commit("SET_REDIRECTPARAMS", {
          goodsId: this.goodsId,
          goodsType: this.goodsType,
        });
        this.$router.push({
          path: "/login",
          query: {
            redirectPath: "/bookstore/bookstoreDetail",
            // queryParams: paramsJson,
          },
        });
      } else {
        //判断是否购买该商品
        this.payType = this.$route.query.payType
          ? this.$route.query.payType
          : "goods";
        checkIsBuy({ relevanceId: this.goodsId, type: this.payType }).then(
          (res) => {
            console.log("是否已购买响应", res);
            if (res.data === "2") {
              this.$message.error("数据异常");
              return;
            }
            if (res.data === "1") {
              SelGoodsStatus(this.goodsId).then((goodsState) => {
                if (goodsState.data.status == 2) {
                  this.$router.push({
                    path: "/submitOrder/index",
                    query: { id: this.goodsId, from: "bookstore" },
                  });
                  this.isShop = false;
                  this.isAnswer = false;
                } else {
                  this.$message("该商品已下架");
                }
              });
            } else {
              this.isShop = true;
              this.isAnswer = true;
              // this.$message("该商品已购买无需重复购买");
            }
          }
        );
      }
    },
    isPayGoods() {
      //判断是否已经购买该商品
      this.payType = this.$route.query.payType
        ? this.$route.query.payType
        : "goods";
      checkIsBuy({ relevanceId: this.goodsId, type: this.payType }).then(
        (res) => {
          console.log("是否已购买响应", res);
          if (res.data === "2") {
            this.$message.error("数据异常");
            return;
          }
          if (res.data === "1") {
            this.isShop = false;
            this.isAnswer = false;
          } else {
            this.isShop = true;
            this.isAnswer = true;
          }
        }
      );
    },
    //加入购物车
    addCart() {
      let userId = this.$store.getters.userId;
      if (!userId) {
        // let paramsJson = JSON.stringify({ goodsId: this.goodsId, goodsType: this.goodsType })
        this.$store.commit("SET_REDIRECTPARAMS", {
          goodsId: this.goodsId,
          goodsType: this.goodsType,
        });
        this.$router.push({
          path: "/login",
          query: {
            redirectPath: "/bookstore/bookstoreDetail",
            // queryParams: paramsJson,
          },
        });
      } else if (this.addCartState) {
        //加入购物车按钮 防止重复提交
        this.addCartState = false;
        checkIsBuy({ relevanceId: this.goodsId, type: this.payType }).then(
          (res) => {
            console.log("是否已购买响应", res);
            if (res.data === "2") {
              this.$message.error("数据异常");
              return;
            }
            if (res.data === "1") {
              if (!this.isCart) {
                SelGoodsStatus(this.goodsId).then((goodsState) => {
                  if (goodsState.data.status === 2) {
                    IsCartAdd(this.goodsId).then((res) => {
                      if (res.data) {
                        this.$message("该商品已经加入购物车");
                        return;
                      }
                      CartAdd(this.goodsId, userId).then((res) => {
                        if (res.code == 200) {
                          this.$store.commit(
                            "addGoodsIntoCartCart",
                            this.goodsId
                          );
                          this.$message({
                            message: "加入购物车成功",
                            type: "success",
                          });
                          this.isCart = true;
                        }
                      });
                    });
                  } else {
                    this.$message("该商品已下架");
                  }
                });
              } else {
                this.$message("该商品已经加入购物车");
              }
            } else {
              this.$message("该商品已购买无需再加入购物车");
            }
          }
        );
        setTimeout(() => {
          this.addCartState = true;
        }, 3000);
      }
    },
    //是否加入购物车
    isAddCart() {
      let userId = this.$store.getters.userId;
      IsCartAdd(this.goodsId).then((res) => {
        if (res.code == 200) {
          if (res.data) {
            // this.$message("您已经加入购物车");
            this.isCart = true;
          } else {
            this.isCart = false;
          }
        }
      });
    },
    //加入书架
    addBookshelf() {
      if (this.goodsInfo.status != 2) {
        this.$message("该商品已下架");
        return;
      }
      //判断是否登录
      let userId = this.$store.getters.userId;
      if (!userId) {
        this.$store.commit("SET_REDIRECTPARAMS", {
          goodsId: this.goodsId,
          goodsType: this.goodsType,
        });
        this.$router.push({
          path: "/login",
          query: {
            redirectPath: "/bookstore/bookstoreDetail",
            // queryParams: paramsJson,
          },
        });
      } else {
        this.query.goodsId = this.goodsId;
        if (!this.isBookshelf) {
          SelGoodsStatus(this.goodsId).then((goodsState) => {
            if (goodsState.data.status === 2) {
              BookshelfAdd(this.query).then((res) => {
                if (res.code == 200) {
                  this.$message({
                    message: "加入书架成功",
                    type: "success",
                  });
                  this.isBookshelf = true;
                }
              });
            } else {
              this.$message("该商品已下架");
            }
          });
        } else {
          // this.$message("该商品已经加入书架");
          //移出书架
          this.query.goodsId = this.goodsId;
          delBookshelf(this.query).then((res) => {
            if (res.data) {
              this.isBookshelf = false;
              this.$message({
                message: "移出书架",
                type: "success",
              });
            }
          });
        }
      }
    },
    // 是否加入书架
    isAddBookshelf() {
      this.query.goodsId = this.goodsId;
      IsBookshelfAdd(this.query).then((res) => {
        if (res.code == 200) {
          if (res.data) {
            // this.$message("该商品已经加入书架");
            this.isBookshelf = true;
          } else {
            this.isBookshelf = false;
          }
        }
      });
    },
    //手机端观看
    phoneLook() {
      this.$alert("此功能正在开发中，敬请期待", "提示信息");
    },
    //在线预览
    onlinePreview(e) {
      //判断是否登录
      let userId = this.$store.getters.userId;
      if (!userId) {
        this.$store.commit("SET_REDIRECTPARAMS", {
          goodsId: this.goodsId,
          goodsType: this.goodsType,
        });
        this.$router.push({
          path: "/login",
          query: {
            redirectPath: "/bookstore/bookstoreDetail",
            // queryParams: paramsJson,
          },
        });
      } else {
        if (this.goodsInfo.goodsType != "paper") {
          //判断是从专题进入还是书城进入
          if (this.subjectId) {
            this.$router.push({
              path: "/onlineReading",
              query: {
                goodsId: this.goodsId,
                resourceType: "subject",
                resource: "bookCity",
                relevanceId: this.subjectId,
                isTrial: e, //活页的试读
              },
            });
          } else {
            this.$router.push({
              path: "/onlineReading",
              query: {
                goodsId: this.goodsId,
                resourceType: "goods",
                resource: "bookCity",
                relevanceId: this.goodsId,
                isTrial: e, //活页的试读
              },
            });
          }
        } else {
          //在线预览
          if (this.subjectId) {
            this.$router.push({
              path: "/bookstore/PreviewOnline",
              query: {
                testPaperId: this.goodsInfo.paperId,
              },
            });
          } else {
            this.$router.push({
              path: "/bookstore/PreviewOnline",
              query: {
                testPaperId: this.goodsInfo.paperId,
              },
            });
          }
        }
      }
    },
    //开始考试
    goAnswer() {
      this.$router.push({
        path: "/bookstore/answer",
        query: {
          goodsId: this.goodsId,
          resourceType: "goods",
          resource: "bookCity",
          relevanceId: this.goodsId,
        },
      });
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getList2();
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getList2();
    },
    changeSrc(index) {
      this.currImg = index;
    },
    share() {
      this.$alert("此功能正在开发中，敬请期待", "提示信息");
    },
    refreshList2() {
      this.getList2();
    },
    getoc_resource_file() {
      //来源
      teachingAttrList("oc_resource_file").then((res) => {
        this.goodsTypeList = res.data;
      });
    },
  },
};
